<template>
  <div>
    <br>
    <p>Costo Promedio</p>
    <div class="col-lg-6 verticalAlign">
      <datepicker v-model="fechaInicio" placeholder="Seleccione el Inicio"  ></datepicker>
    </div>
    <div class="col-lg-6 verticalAlign">
      <datepicker v-model="fechaFin" placeholder="Seleccione el Final"  ></datepicker>
    </div>
    <br/>
    <br/>
    <div align ="center" class="col-lg-12" v-if="fechaInicio !== ''">
      <p>Desde: {{ fechaInicio }}  hasta: {{ fechaFin }} </p>
    </div>
    <div align ='center' class="col-lg-6">
      <!-- Single button triggers two "<b-collapse>" components -->
      <div>
        <b-button v-b-toggle.collapse-a.collapse-b>Cantidad de Eventos <span class="producto"> {{ nEventos }}</span></span></b-button>
      </div>
      <div>
        <b-button v-b-toggle.collapse-a.collapse-b>Cantidad de Comensales <span class="producto"> {{ nComensales }}</span></b-button>
      </div>
      <div>
        <b-button v-b-toggle.collapse-a.collapse-b>Total Cobrado <span class="producto">$ {{ totalCobrado }}</span></b-button>
      </div>
      <div>
        <b-button v-b-toggle.collapse-a.collapse-b>Eventos Adicionales <span class="producto"> {{ eventosAdicionales }}</span></b-button>
      </div>
      <div>
        <b-button v-b-toggle.collapse-a.collapse-b>Eventos de Recaudacion <span class="producto">$ {{ eventosRecaudacion }}</span></b-button>
      </div>
      <div>
        <b-button v-b-toggle.collapse-a.collapse-b>Venta de vinos <span class="producto">$ {{ totalVentaVinos }}</span></b-button>
      </div>
      <div>
        <b-button v-b-toggle.collapse-a.collapse-b>Ventas <span class="producto">$ {{ eventosRecaudacion }}</span></b-button>
      </div>
      <div>
        <b-button v-b-toggle.collapse-a.collapse-b>Cancelaciones <span class="producto">$ {{ eventosRecaudacion }}</span></b-button>
      </div>
      <div>
        <b-button v-b-toggle.collapse-a.collapse-b>Total Cobrado <span class="producto">$ {{ eventosRecaudacion }}</span></b-button>
      </div>
      <div>
        <b-button v-b-toggle.collapse-a.collapse-b>Gasto Insumo<span class="producto">$ {{ eventosRecaudacion }}</span></b-button>
      </div>
      <div>
        <b-button v-b-toggle.collapse-a.collapse-b>Gasto Activo <span class="producto">$ {{ eventosRecaudacion }}</span></b-button>
      </div>

      <!-- Elements to collapse -->
      <b-collapse id="collapse-a" class="mt-2">
        <b-card>I am collapsible content A!</b-card>
      </b-collapse>
      <b-collapse id="collapse-b" class="mt-2">
        <b-card>I am collapsible content B!</b-card>
      </b-collapse>
    </div>
  </div>
</template>

<script>
import Datepicker from 'vuejs-datepicker';
const axios = require('axios');
const moment = require("moment")
  export default {
    components: {Datepicker},
    data() {
      return {
        fechaInicio: '',
        fechaFin: '',
        nEventos: 0,
        nComensales: 0,
        totalCobrado: 0,
        eventosAdicionales: 0,
        eventosRecaudacion: 0,
        totalVentaVinos: 0
      }
    },
    computed: {
      sortOptions() {
        // Create an options list from our fields
        return this.fields
          .filter(f => f.sortable)
          .map(f => {
            return { text: f.label, value: f.key }
          })
      }
    },
    watch:{
      fechaInicio (val) {
        this.fechaInicio = moment(val).format('YYYY-MM-DD')
        if (this.fechaFin !== '') {
          this.numeroComensales()
          this.numeroEventos()
        }
      },
      fechaFin (val) {
        // eslint-disable-next-line no-console
        this.fechaFin = moment(val).format('YYYY-MM-DD')
        if (this.fechaFin !== '') {
          this.numeroEventos()
          this.numeroComensales()
          this.cobrado()
          this.numeroEventosAdicionales()
          this.numeroEventosRecaudacion()
          this.ventaVinos()
        }
      }
    },
    methods: {
      numeroEventos () {
        this.getData('numeroEventos').then((data) => {
            this.nEventos = data;
        })
      },
      numeroComensales () {
        this.getData('numeroComensales').then((data) => {
            this.nComensales = data;
        })
      },
      cobrado () {
        this.getData('totalCobrado').then((data) => {
            this.totalCobrado = this.formatPrice(data);
        })
      },
      numeroEventosAdicionales () {
        this.getData('eventosAdicionales').then((data) => {
            this.eventosAdicionales = data;
        })
      },
      numeroEventosRecaudacion () {
        this.getData('eventosRecaudacion').then((data) => {
            this.eventosRecaudacion = this.formatPrice(data);
        })
      },
      ventaVinos () {
        this.getData('ventaVinos').then((data) => {
            this.totalVentaVinos = this.formatPrice(data);
        })
      },
      getData: function (opcion) {
        return new Promise((resolve, reject) => {
          try {
            let payload = {
              fecha1: this.fechaInicio,
              fecha2: this.fechaFin,
              opcion: opcion
            }
            let personForm = this.toFormData(payload);
            axios
            .post('./costoPromedio.php', personForm)
            .then(response => {
              resolve(response.data)
            })
            .catch(error => {
              this.errored = true
              // eslint-disable-next-line no-console
              console.log(error)
            })
            .finally(() => this.loading = false)
          }
          catch (error) {
            reject(error)
          }
        })
      },
      toFormData(obj) {
        let formData = new FormData();
        for(let key in obj) {
          formData.append(key, obj[key]);
        }
        return formData;
      },
      formatPrice(value) {
        let val = (value/1).toFixed(2).replace('.', ',')
        return val.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".")
      }
    }
  }
</script>
<style>
  .producto {
    color: darkorange;
  }
</style>
