<script>
import { Bar } from 'vue-chartjs'
const axios = require('axios');
var _ = require('lodash');
export default {
  extends: Bar,
  props: {
    event: {
      type: Array
    },
    year: {
      type: Array
    }
  },
  data () {
      return {
        dat: [],
        label: [],
        colorNumber: 1,
        backgroundColor: [{
          1: ['#922B21', '#922B21', '#922B21', '#922B21', '#922B21', '#922B21', '#922B21', '#922B21', '#922B21', '#922B21', '#922B21', '#922B21', '#922B21', '#922B21'],
          2: ['#1A5276', '#1A5276', '#1A5276', '#1A5276', '#1A5276', '#1A5276', '#1A5276', '#1A5276', '#1A5276', '#1A5276', '#1A5276', '#1A5276', '#1A5276', '#1A5276'],
          3: ['#F4D03F', '#F4D03F', '#F4D03F', '#F4D03F', '#F4D03F', '#F4D03F', '#F4D03F', '#F4D03F', '#F4D03F', '#F4D03F', '#F4D03F', '#F4D03F', '#F4D03F', '#F4D03F'],
          4: ['#3498DB', '#3498DB', '#3498DB', '#3498DB', '#3498DB', '#3498DB', '#3498DB', '#3498DB', '#3498DB', '#3498DB', '#3498DB', '#3498DB', '#3498DB', '#3498DB'],
          5: ['#9B59B6', '#9B59B6', '#9B59B6', '#9B59B6', '#9B59B6', '#9B59B6', '#9B59B6', '#9B59B6', '#9B59B6', '#9B59B6', '#9B59B6', '#9B59B6', '#9B59B6', '#9B59B6'],
          6: ['#DC7633', '#DC7633', '#DC7633', '#DC7633', '#DC7633', '#DC7633', '#DC7633', '#DC7633', '#DC7633', '#DC7633', '#DC7633', '#DC7633', '#DC7633', '#DC7633'],
          7: ['#5D6D7E', '#5D6D7E', '#5D6D7E', '#5D6D7E', '#5D6D7E', '#5D6D7E', '#5D6D7E', '#5D6D7E', '#5D6D7E', '#5D6D7E', '#5D6D7E', '#5D6D7E', '#5D6D7E', '#5D6D7E'],
          8: ['#33F6FF', '#33F6FF', '#33F6FF', '#33F6FF', '#33F6FF', '#33F6FF', '#33F6FF', '#33F6FF', '#33F6FF', '#33F6FF', '#33F6FF', '#33F6FF', '#33F6FF', '#33F6FF'],
          9: ['#3346FF', '#3346FF', '#3346FF', '#3346FF', '#3346FF', '#3346FF', '#3346FF', '#3346FF', '#3346FF', '#3346FF', '#3346FF', '#3346FF', '#3346FF', '#3346FF'],
          10: ['#319900', '#319900', '#319900', '#319900', '#319900', '#319900', '#319900', '#319900', '#319900', '#319900', '#319900', '#319900', '#319900', '#319900'],
          11: ['#FFC900', '#FFC900', '#FFC900', '#FFC900', '#FFC900', '#FFC900', '#FFC900', '#FFC900', '#FFC900', '#FFC900', '#FFC900', '#FFC900', '#FFC900', '#FFC900'],
          12: ['#292929', '#292929', '#292929', '#292929', '#292929', '#292929', '#292929', '#292929', '#292929', '#292929', '#292929', '#292929', '#292929', '#292929']
        }]
      }
    },
    watch: {
      event(eventosx) {
        this.colorNumber = 0
        this.dat = []
        var t = 0
        this.getData().then((data) => {
          let totales = []
          _.forEach(data, (val, key) => {
            this.colorNumber = 0
            // t+= val.total
            // totales.push(val.total)
            _.forEach(eventosx, (ev) => {
              _.forEach(val, (final) => {
                if (final.tipo === ev) {
                  totales.push(final.total)
                }
              })
              this.colorNumber++
              this.dat.push({
                label: ev + key,
                backgroundColor: this.backgroundColor[0][this.colorNumber],
                data: totales
              })
            })
          })
          totales.push(0)
          totales.push(t)
          this.renderChart({
            labels: ['Enero', 'Febrero', 'Marzo', 'Abril', 'Mayo', 'Junio', 'Julio', 'Agosto', 'Septiembre', 'Octubre', 'Noviembre', 'Diciembre', ' -- ', 'Totales'],
            // labels: this.label,
            datasets: this.dat
          })
        })
      }
    },
  mounted () {
  },
  methods: {
    getData: function async () {
       return new Promise((resolve, reject) => {
        try {
          let payload = {
            opcion: 'generalChart2',
            years: this.year,
            eventos: this.event
          }   
          let personForm = this.toFormData(payload);
          // eslint-disable-next-line no-console
          console.log("payload XXX", personForm)
          axios
          .post('./mysqli.php', personForm)
          .then(response => {
              resolve(response.data)
          })
          .catch(error => {
            this.errored = true
            // eslint-disable-next-line no-console
            console.log(error)
          })
          .finally(() => this.loading = false)
        }
        catch (error) {
            reject(error)
        }
      })
    },
      toFormData: function(obj) {
          let formData = new FormData();
          for(let key in obj) {
              formData.append(key, obj[key]);
            }
            return formData;
        }
  }
}
</script>
